.container{
    display:flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
}

.heading{
    font-family: "Roboto";
    font-size: 40px;
    color: #32404d;

}
.count{
    font-size: 80px;
    font-family: 'Roboto';
    color: #2e3e4e;
    margin-top: 8px;
    margin-bottom: 28px ;
}
.button{
    font-size: 16px;
    font-weight: 400;
    font-family: 'Roboto';
    color: white;
    padding-top: 8px;
    padding-bottom: 8px;
    padding-right: 15px;
    padding-left: 15px;
    margin: 8px;
    background-color: #7c69e9;
    border: none;
    border-radius: 4px;

}